import {Link} from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import "./postIndex.css"

let selection=true;
function clickIndexIcon() {
    if(selection){
        document.getElementsByClassName("backgroundLittlePanel")[0].setAttribute("style", "display: none;");
        document.getElementsByClassName("backgroundBigPanel")[0].setAttribute("style", "display: inline-block;");
    }
    else{
        document.getElementsByClassName("backgroundLittlePanel")[0].setAttribute("style", "display: inline-block;");
        document.getElementsByClassName("backgroundBigPanel")[0].setAttribute("style", "display: none;");
    }
    selection = !selection;
}

const PostIndex = ({linksData, selectedItem}) => (
    <div>
        <link rel="stylesheet"
              href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css"></link>

        <div className="backgroundLittlePanel">
            <i style={{fontSize: '22px'}} className="fa fa-list iconpostIndex" onClick={() => clickIndexIcon()}></i>
            <p style={{paddingLeft: '50px', position: 'relative', top: '13px'}}>Contenido</p>
        </div>
        <div className="backgroundBigPanel">
            <i style={{fontSize: '22px'}} className="fa fa-list iconpostIndex" onClick={() => clickIndexIcon()}></i>
            <ul className="postIndexList">
                <a className="postIndexTitle">Contenido</a>
                <div style={{textAlign: 'justify', paddingTop: '40px'}}>
                    {linksData.map((item, key) => {
                        return (

                            <li><a className="postIndex" style={{textDecoration: 'underline'}}
                                   href={item.url}>{item.text}</a></li>
                        )
                    })}
                </div>
            </ul>
        </div>

    </div>
)

export default PostIndex