import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import PostIndex from "../components/postIndex/postIndex"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from  "../components/postIndex2/postIndex2";

const Mac = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina B1"
                meta="Toda la información que necesitas sobre la vitamina B1. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B1?"
                description="Toda la información que necesitas sobre la vitamina B1. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B1?"
                lang="es"
            />
            <h1>&iquest;Qu&eacute; es la tiamina o la vitamina B1?</h1>
            <PostIndex2   linksData={[
                {"url": "#alimentos", "text": "Alimentos"},
                {"url": "#beneficios", "text": "Beneficios"},
                {"url": "#deficiencia", "text": "Síntomas de deficiencia"},
                {"url": "#funcion", "text": "Fucnión"},
                {"url": "#EfectosSecundarios", "text": "Los efectos secundarios"},
            ]}
            />
            <hr />
            <p class="introductionText">La vitamina B1, tiamina, o tiamina, permite al cuerpo utilizar los carbohidratos como energ&iacute;a. Es
                esencial para el metabolismo de la glucosa, y juega un papel clave en la funci&oacute;n de los nervios,
                los m&uacute;sculos y el coraz&oacute;n.</p>
            <hr />
            <p></p>

            <p>La vitamina B1 es una vitamina soluble en agua, como todas las <a class="link" href="/vitaminaB">vitaminas del complejo B.</a></p>
            <p></p>
            <p>Las vitaminas se clasifican seg&uacute;n los materiales en los que se disuelven. Algunas se disuelven en
                agua y otras en grasa. Las vitaminas solubles en agua son transportadas a trav&eacute;s del torrente
                sangu&iacute;neo. Lo que el cuerpo no utiliza se elimina en la orina.</p>
            <p></p>
            <h2 name="alimentos">Alimentos que tienen vitamina B1</h2>
            <p>Hay altas concentraciones de vitamina B1 en los cereales,
                as&iacute; como en la levadura, la carne de vacuno, el cerdo, las nueces, los granos enteros y las
                legumbres.</p>
            <p></p>
            <p>También la incluyen la coliflor, h&iacute;gado, naranjas, huevos, patatas,
                esp&aacute;rragos y col rizada.</p>
            <p></p>
            <p>Otras fuentes incluyen la levadura de cerveza y la melaza de ca&ntilde;a negra.</p>
            <p></p>
            <p>Los cereales para el desayuno y los productos elaborados con harina blanca o arroz blanco pueden estar
                enriquecidos con vitamina B.</p>
            <p></p>
            <p>En los Estados Unidos, las personas consumen alrededor de la mitad de su ingesta de vitamina B1 en
                alimentos que contienen tiamina de forma natural, mientras que el resto proviene de alimentos que
                est&aacute;n fortificados con la vitamina.</p>
            <p></p>
            <p>Calentar, cocinar y procesar los alimentos, y hervirlos en agua, destruye la tiamina. Como la vitamina B1
                es soluble en el agua, se disuelve en el agua de cocci&oacute;n. <b>El arroz blanco que no
                est&aacute; enriquecido contendr&aacute; s&oacute;lo una d&eacute;cima parte de la tiamina disponible en
                    el arroz integral.</b></p>
            <p></p>
            <p>La Oficina de Suplementos Diet&eacute;ticos (ODS) de los Institutos Nacionales de Salud (NIH)
                se&ntilde;ala que una porci&oacute;n de cereal fortificado para el desayuno proporciona 1,5 miligramos
                (mg) de tiamina, que es m&aacute;s del 100 por ciento de la cantidad diaria recomendada.</p>
            <p></p>
            <p>Una rebanada de pan de trigo integral contiene 0,1 mg, o el 7 por ciento del requerimiento diario. El
                queso, el pollo y las manzanas no contienen tiamina.</p>
            <p></p>
            <p><b>Los seres humanos necesitan un suministro continuo de vitamina B1, porque no se almacena en el cuerpo.
                Debe formar parte de la dieta diaria.</b></p>
            <p></p>
            <p></p>
            <h2 name="beneficios">Beneficios de la vitamina B1</h2>
            <p>La vitamina B1, o tiamina, ayuda a prevenir complicaciones en el sistema nervioso, el cerebro, los
                m&uacute;sculos, el coraz&oacute;n, el est&oacute;mago y los intestinos. Tambi&eacute;n
                est&aacute; involucrada en el flujo de electrolitos hacia y desde las c&eacute;lulas musculares y
                nerviosas.</p>
            <p></p>
            <p>Ayuda a prevenir enfermedades como el beriberi, que implica trastornos del coraz&oacute;n, los nervios y
                el sistema digestivo.</p>
            <p></p>
            <h3>Usos en medicina</h3>
            <p>Entre los pacientes que pueden recibir tiamina para tratar los bajos niveles de vitamina B1 se encuentran
                los que tienen neuritis perif&eacute;rica, que es una inflamaci&oacute;n de los nervios fuera del
                cerebro, o pelagra.</p>
            <p></p>
            <p>Algunos atletas toman suplementos de tiamina para mejorar su rendimiento.</p>
            <p>Las personas con colitis ulcerosa, diarrea persistente y falta de apetito tambi&eacute;n pueden recibir
                tiamina. Los que est&aacute;n en coma pueden recibir inyecciones de tiamina.</p>
            <p></p>
            <p></p>
            <p>Otras afecciones en las que los suplementos de tiamina pueden ayudar incluyen:</p>
            <p></p>
            <ul>
                <li>Cataratas</li>
                <li>Glaucoma y otros problemas de visi&oacute;n</li>
                <li>El s&iacute;ndrome cerebeloso, un tipo de da&ntilde;o cerebral</li>
                <li>C&aacute;ncer de cuello de &uacute;tero</li>
                <li>Dolor diab&eacute;tico</li>
                <li>Estr&eacute;s</li>
                <li>Enfermedad card&iacute;aca</li>
                <li>Enfermedad renal en pacientes con diabetes tipo 2</li>
                <li>Mareo</li>
                <li>Un sistema inmunol&oacute;gico debilitado.</li>
            </ul>
            <p></p>
            <h2 name="deficiencia">S&iacute;ntomas de deficiencia de vitamina B1</h2>
            <p>La deficiencia de vitamina B1 suele provocar beriberi, una afecci&oacute;n que se caracteriza por
                problemas con los nervios perif&eacute;ricos y la emaciaci&oacute;n.</p>
            <p></p>
            <p>Se puede desarrollar p&eacute;rdida de peso y anorexia.</p>
            <p></p>
            <p>Puede haber problemas mentales, incluyendo confusi&oacute;n y p&eacute;rdida de la memoria a corto
                plazo.</p>
            <p></p>
            <p>Los m&uacute;sculos pueden debilitarse y pueden aparecer s&iacute;ntomas cardiovasculares, por ejemplo,
                un coraz&oacute;n agrandado.</p>
            <p></p>
            <p></p>
            <p></p>
            <h3>&iquest;Cu&aacute;nta vitamina B1 necesitamos?</h3>
            <p>En los EE.UU., la dosis diaria recomendada (RDA) de tiamina tomada por v&iacute;a oral es de 1,2 mg para
                los hombres y 1,1 mg para las mujeres mayores de 18 a&ntilde;os. Las mujeres embarazadas o en
                per&iacute;odo de lactancia de cualquier edad deben consumir 1,4 mg cada d&iacute;a.</p>
            <p></p>
            <h3>&iquest;Qui&eacute;nes corren el riesgo de padecer deficiencia de vitamina B1?</h3>
            <p>Las personas con una dieta pobre, c&aacute;ncer, &quot;n&aacute;useas matinales&quot; durante el
                embarazo, cirug&iacute;a bari&aacute;trica y hemodi&aacute;lisis tienen riesgo de deficiencia de
                tiamina.</p>
            <p></p>
            <p>Las personas que beben alcohol regularmente en exceso pueden tener una deficiencia, ya que pueden no
                absorber la tiamina de sus alimentos.</p>
            <p></p>
            <p>El s&iacute;ndrome de Wernicke-Korsakoff es un trastorno que afecta a las personas con alcoholismo
                cr&oacute;nico. Est&aacute; relacionado con la falta de tiamina, y puede ser fatal si no se trata.</p>
            <p></p>
            <p>Las personas con el s&iacute;ndrome de Wernicke-Korsakoff y las que se est&aacute;n retirando del alcohol
                pueden recibir inyecciones de tiamina para ayudarles a recuperarse.</p>
            <p></p>
            <p>Otras enfermedades, como el VIH, pueden reducir la absorci&oacute;n de nutrientes, lo que puede provocar
                una deficiencia de vitamina B1.</p>
            <p></p>
            <h2 name="funcion">Funci&oacute;n de la vitamina B1</h2>
            <p>Todas las vitaminas B son solubles en agua. Ayudan a convertir los carbohidratos, las grasas y las
                prote&iacute;nas en energ&iacute;a o glucosa.</p>
            <p></p>
            <p>Las vitaminas B son necesarias para mantener el h&iacute;gado, la piel, el cabello y los ojos saludables.
                Tambi&eacute;n desempe&ntilde;an un papel en el sistema nervioso y son necesarias para el buen
                funcionamiento del cerebro.</p>
            <p></p>
            <p>Las vitaminas B se denominan a veces vitaminas antiestr&eacute;s, porque refuerzan el sistema
                inmunol&oacute;gico del cuerpo en momentos de estr&eacute;s.</p>
            <p></p>
            <h2 name="EfectosSecundarios">Efectos secundarios</h2>
            <p>Las pruebas no confirman ning&uacute;n da&ntilde;o por exceso de vitamina B1, pero la
                Administraci&oacute;n de Drogas y Alimentos de los Estados Unidos (FDA) advierte sobre el uso de
                suplementos.</p>
            <p></p>
            <p>Insta a las personas a consultar con su proveedor de atenci&oacute;n m&eacute;dica antes de usar
                suplementos con o como sustituto de alimentos, y pide al p&uacute;blico que busque el consejo de un
                m&eacute;dico sobre c&oacute;mo mejorar su salud, en lugar de autodiagnosticarse.</p>
            <p></p>

        </Layout>
    )
}

export default Mac
